/* Navbar */
.navbarLink{
  padding-right: .5rem; 
  padding-left: .5rem; 
  display: inline-block; 
  padding: 25px;
}
.navbarLink a {
  text-decoration: none!important;
  color:white;
  position: relative;
}
/* drawing underline */
.navbarLink a::before {
  text-decoration: none!important;
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -12px;
  left: 0;
  background-color: rgb(255, 115, 0);
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.navbarLink a:hover:before{
  text-decoration: none;
  -webkit-transform: scaleX(1.5);
          transform: scaleX(1.5);
  
}
.navbarLink a:hover {
  text-decoration: none!important;
  color: rgb(255, 115, 0);
}
.alpha{
  background:#1a1a1a;   
}

.navbar-brand a{
  text-decoration: none !important;
