/* Card */
.wrapper-card-img{
  position: relative; 
  /* ไม่กระโดด */
  overflow: hidden; 
  /* ซ่อนภาพ*/
}
.wrapper-card-img::after{
  content:'';
  padding-top:56.25%;
  display: block;
  height: 0;
  
}

.wrapper-card-img img{
  position: absolute;
  top: 0;
  left: 0;
}
.wrapper-card-img img:hover{
-webkit-transform: scale(1.75);
        transform: scale(1.75);
-webkit-transition: 1s;
transition: 1s;
}
.card-body{
max-height: 170px;
overflow: hidden;
height:170px;
}
.card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border-radius: .25rem;
}
.shadows{
    box-shadow: 0px 0px 10px 0.5px rgba(206, 206, 206, 0.5);
}

.UnderLine{
  border-bottom: solid 5px rgb(255, 115, 0) ;
  width: 150px;
  margin: 0 auto ;
}
